// This file convers sequence of Notes into Abc Notation
import {join, minBy, filter} from 'lodash'

const noteNameIndex: string[] = ['C', '^C', 'D', '^D', 'E', 'F', '^F', 'G', '^G', 'A', '^A', 'B']
const octaves: string[] = ['C,,,,', 'C,,,', 'C,,', 'C,', 'C', 'c', "c'", "c''", "c'''"]
const octaveMiddle: number = 60 // Middle C 
const firstNote: number = 21 // A0
const lastNote : number = 108 // C8

// TODO: keySignature determines abcnotenames. e.g. in D, 
// type keySignature = 'C' | 'D' | 'Dmin' 

// const noteNameIndex: string[] = []

export function midiNumberToAbcNoteName(n: number/*, keySig: number*/): string {
    if (n < firstNote || n > lastNote) {
        return 'C,,,,' // still return something, but obviously outside the keyboard
    }
    let noteName = noteNameIndex[(n - octaveMiddle + 120) % 12]
    let octave = octaves[(n - 12) / 12 | 0]
    if (octave[0] === 'c') {
        noteName = noteName.toLowerCase()
    }
    octave = octave.slice(1)
    return `${noteName}${octave}`
}

export function slotToAbcDuration(slots: number, beatsPerMeasure: number, defaultBeatDurationInSlots: number): string {
    if (slots === defaultBeatDurationInSlots) {
        return ''
    }
    if (slots % defaultBeatDurationInSlots === 0) {
        return `${slots / defaultBeatDurationInSlots}`
    }
    return `${slots}/${defaultBeatDurationInSlots}`
}

export type Note = {
    n: number, // note number
    begin: number, // noteon timestamp relative to record start
    duration: number, // 
    velocity: number, // velocity 
}

type MeasureNotation = {
    measureIndex: number
    notations: string
}

type Slots =  {
    [key: number]: Note[]
}
type ProcessEnv = {    
    bpm: number // = 60
    msPerMinute: number //  = 60 * 1000
    beatsPerMeasure: number //  = 4
    measureDuration: number //  = msPerMinute * beatsPerMeasure / bpm | 0 // in ms
    beatDuration: number //  = msPerMinute / bpm | 0 // in ms
    defaultBeat: number //  = 4 // 1/4 notes as the default
    resolution: number //  = 8 // minimal using 1/8 notes
    slotDuration: number //  = beatDuration * defaultBeat / resolution | 0
    slotsPerMeasure: number //  = beatsPerMeasure * resolution / defaultBeat | 0
    defaultBeatInSlots: number //  = resolution / defaultBeat | 0
}
function processVoice(maxSlot: number, slots: Slots, processEnv: ProcessEnv) : MeasureNotation[] {
    let newAbcNotation = ''
    let numMeasures = 0
    let measures: MeasureNotation[] = []
    let lastSlotReaches = 0
    for (let i = 0; i <= maxSlot; i++){
        if (i > 0 && i % processEnv.slotsPerMeasure === 0) {
            // newAbcNotation += '|'
            if (i > lastSlotReaches) {
                newAbcNotation += `z${slotToAbcDuration(i - lastSlotReaches, processEnv.beatsPerMeasure, processEnv.defaultBeatInSlots)}`
            }
            measures.push({
                measureIndex: numMeasures,
                notations: newAbcNotation
            })
            newAbcNotation = ''
            numMeasures++
            lastSlotReaches = i
        }
        if (i in slots && slots[i].length > 0) {
            let slotMaxDuration = 1
            for (let j = i + 1; j <= maxSlot + processEnv.slotsPerMeasure; j++) {
                if (j % processEnv.slotsPerMeasure === 0) {
                    slotMaxDuration = j - i
                    break
                }
                if (j in slots && slots[j].length > 0) {
                    slotMaxDuration = j - i
                    break
                }
            }
            if (i > lastSlotReaches) {
                newAbcNotation += `z${slotToAbcDuration(i - lastSlotReaches, processEnv.beatsPerMeasure, processEnv.defaultBeatInSlots)}`
            }
            let maxDurationsInSlots = 0
            const notes = slots[i].map((n: Note) => {
                const durationInSlots = (n.duration + processEnv.slotDuration * 0.8) / processEnv.slotDuration | 0
                if (durationInSlots > slotMaxDuration) {
                    let nRest: Note = {
                        ...n,
                        duration: n.duration - slotMaxDuration * processEnv.slotDuration
                    }
                    if (!(i + slotMaxDuration in slots)) {
                        slots[i + slotMaxDuration] = [nRest]
                    } else {
                        slots[i + slotMaxDuration].push(nRest)
                    }
                    maxSlot = Math.max(maxSlot, i + slotMaxDuration)
                    const note = `${midiNumberToAbcNoteName(n.n)}${slotToAbcDuration(slotMaxDuration, processEnv.beatsPerMeasure, processEnv.defaultBeatInSlots)}-`
                    maxDurationsInSlots = Math.max(slotMaxDuration, durationInSlots)
                    return note
                }
                const note = `${midiNumberToAbcNoteName(n.n)}${slotToAbcDuration(durationInSlots, processEnv.beatsPerMeasure, processEnv.defaultBeatInSlots)}`
                maxDurationsInSlots = Math.max(maxDurationsInSlots, durationInSlots)
                return note
            })
            if (notes.length > 1){
                newAbcNotation += `[${join(notes, '')}]`
            } else {
                newAbcNotation += notes[0]
            }
            lastSlotReaches = i + maxDurationsInSlots
        } else {
        // newAbcNotation += `z${slotToAbcDuration(1, beatsPerMeasure, defaultBeatInSlots)}`
        }
    }
    if (newAbcNotation.length > 0){
        measures.push({
            measureIndex: numMeasures,
            notations: newAbcNotation
        })
        numMeasures++
    }
    return measures
}


function processAll(sequenceBeginTime: number, sequence: Note[], processEnv: ProcessEnv): string {
    let maxSlot = 0
    let slots: Slots = {}
    let notes = sequence.forEach((n: Note, index: number) => {
        const timeSinceBegin = n.begin - sequenceBeginTime
        const measureN = (timeSinceBegin + processEnv.slotDuration / 2) / processEnv.measureDuration | 0
        const slotInMeasureN = Math.max(0, timeSinceBegin - measureN * processEnv.measureDuration + processEnv.slotDuration / 2) / processEnv.slotDuration | 0
        const slotN = measureN * processEnv.slotsPerMeasure + slotInMeasureN

        maxSlot = Math.max(slotN, maxSlot)
        if (slotN in slots) {
            slots[slotN].push(n)
        } else {
            slots[slotN] = [n]
        }
    })
    const measurePerLine = 3

    let treble8: Slots = {}
    let bass: Slots = {}
    const octaveMiddle = 60 // Middle C
    for (let i = 0; i <= maxSlot; i++){
        if (i in slots) {
            treble8[i] = filter(slots[i], (n)=> n.n >= octaveMiddle)
            bass[i] = filter(slots[i], (n)=> n.n < octaveMiddle)
        }
    }
    const treble8Measures = processVoice(maxSlot, treble8, processEnv)
    const bassMeasures = processVoice(maxSlot, bass, processEnv)
    
    const header = `X: 1
T: Untitled
M: ${processEnv.beatsPerMeasure}/${processEnv.defaultBeat}
L: 1/8
R: reel
K: D`
    let abcNotation = [
        header,
        '%%score (T1) (B1)',
        'V:T1  clef=treble-8  name="Tenore I"   snm="T.I"',
        'V:B1  clef=bass      name="Basso I"    snm="B.I"  octave=-2',
    ]
    const numMeasures = Math.max(treble8Measures.length, bassMeasures.length)
    for (let i = 0; i < numMeasures; i += measurePerLine) {
        abcNotation.push(`% ${i + 1}`)
        abcNotation.push(`[V:T1]${treble8Measures.slice(i, i + measurePerLine).map(m => m.notations).join(' | ')} |`)
        abcNotation.push(`[V:B1]${bassMeasures.slice(i, i + measurePerLine).map(m => m.notations).join(' | ')} |`)
    }

    return abcNotation.join('\n')
}
export function convertToAbcNotation(sequence: Note[]) : string {

    const sequenceBeginTime = minBy(sequence, 'begin')?.begin
    if (sequenceBeginTime === undefined) {
        return ''
    }

    const bpm = 60
    const msPerMinute = 60 * 1000
    const beatsPerMeasure = 4
    const measureDuration = msPerMinute * beatsPerMeasure / bpm | 0 // in ms
    const beatDuration = msPerMinute / bpm | 0 // in ms
    const defaultBeat = 4 // 1/4 notes as the default
    const resolution = 8 // minimal using 1/8 notes
    const slotDuration = beatDuration * defaultBeat / resolution | 0
    const slotsPerMeasure = beatsPerMeasure * resolution / defaultBeat | 0
    const defaultBeatInSlots = resolution / defaultBeat | 0
    const env: ProcessEnv = {
        bpm, msPerMinute, beatsPerMeasure, measureDuration, beatDuration, defaultBeat, resolution, slotDuration, slotsPerMeasure, defaultBeatInSlots
    }

    let voices = []
    return processAll(sequenceBeginTime, sequence, env)
}
