import React from 'react'

import { connect } from 'react-redux'

import { createStyles, Theme, makeStyles } from '@material-ui/core/styles'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import TextField from '@material-ui/core/TextField'
import Alert from '@material-ui/lab/Alert'
import Button from '@material-ui/core/Button'

import Abcjs from 'react-abcjs'
import abcjs from 'abcjs'

import {uniqueId} from 'lodash'

//import abcjs from "abcjs"
//import 'abcjs/abcjs-audio.css'
import { RootState } from '../app/rootReducer'

const useStyles = makeStyles((theme: Theme) => createStyles({
    root: {
        width: '100%',
    },
    marginTop: {
        marginTop: 8,
    },
    barNoAnimation: {
        animation: 'none'
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'center',
      color: theme.palette.text.secondary,
      position: 'relative'
    },
}))
type SheetProps = {
    abcNotation: string
    setAbcNotation?: (newAbcNotation: string) => void
}

const Sheet : React.FC<SheetProps> = (props) => {
    const classes = useStyles()
    const clickListener = (abcelem: any, tuneNumber: number, classes: any[], analysis: any, drag: any) => {
        console.log(abcelem, tuneNumber, classes, analysis, drag)
    }
    const [editor, setEditor] = React.useState<any>(undefined)
    
    const [paperId, setPaperId] = React.useState<string>(uniqueId("paper-"))
    const [warningId, setWarningId] = React.useState<string>(uniqueId("warning-"))

    

    const textInput = React.useRef(null);
    const paperRef = React.useRef(null);
    const warningRef = React.useRef(null);
    
    React.useEffect(() => {
        if (editor) {
        // console.log(editor?.isDirty())
            editor.fireChanged()
        }
    }, [editor, props.abcNotation])

    React.useEffect(() => {
        const observer = new ResizeObserver((entries: any[]) => {
            const style = window.getComputedStyle(entries[0].target, null)
            const width =  parseInt(style.width) - editor.engraver_controller.renderer.padding.left - editor.engraver_controller.renderer.padding.right
            editor.paramChanged({staffwidth:width})
            console.log(width)
            //editor.fireChanged()
        })
        if (paperRef && paperRef.current) {
            observer.observe(paperRef.current! as Element)
        }
        return () => {
            observer.disconnect()
        }
    }, [editor, paperRef])
    React.useEffect(() => {
        if (textInput && textInput.current &&
            paperRef && paperRef.current &&
            warningRef && warningRef.current 
            ){
            //let textArea = new abcjs.TextArea(textInput.current)
            let e = new abcjs.Editor("outlined-multiline-static", {
                //canvas_id: paperRef.current, 
                paper_id:  paperId,
                warnings_id: warningId
            })
            setEditor(e)
        }
    }, [textInput, paperRef, warningRef, paperId, warningId])

    //const [synth, setSynth] = React.useState<any>(undefined)

    return (
        <Container>
        <Grid container spacing={3}>
            <Grid item xs={8}>
                <Button variant="contained" color="primary" onClick={() => {
                    //if (synth === undefined) {
                    let s = new abcjs.synth.CreateSynth();
                    let el = document.createElement('div')
                    let visualObjs = abcjs.renderAbc(el, props.abcNotation)
                    s.init({visualObj: visualObjs[0]}).then(function (response: any) {
                        console.log("Audio object has been initialized")
                        // console.log(response); // this contains the list of notes that were loaded.
                        // midiBuffer.prime actually builds the output buffer.
                        return s.prime()
                    }).then(function () {
                        console.log("Audio object has been primed")
                        // At this point, everything slow has happened. midiBuffer.start will return very quickly and will start playing very quickly without lag.
                        s.start();
                        console.log("Audio started")
                        return Promise.resolve();
                    }).catch(function (error: any) {
                        if (error.status === "NotSupported") {
                            //stopAudioButton.setAttribute("style", "display:none;");
                            //var audioError = document.querySelector(".audio-error");
                            //audioError.setAttribute("style", "");
                        } else
                            console.warn("synth error", error);
                    });
                        //setSynth(s)
                    //}
                }}>Play</Button> 
                <Paper className={classes.paper} id={paperId} ref={paperRef} style={{padding: 0}}/>
            </Grid>
            <Grid item xs={4}>
                <TextField id="outlined-multiline-static"
                    inputRef={textInput}
                    label="Multiline"
                    multiline
                    fullWidth
                    rows={30}
                    variant="outlined"
                    value={props.abcNotation}
                    onChange={(e) => props.setAbcNotation && props.setAbcNotation(e.target.value)}
                    onSelect={(e) => {
                        console.log('onSelect', e)
                    }}
                    onSelectCapture={(e) => {
                        console.log('onSelectCapture', e)
                    }}
                />
                <Alert severity="warning" id={warningId} ref={warningRef}>This is an error alert â€” check it out!</Alert>

            </Grid>
        </Grid>
        </Container>
    )
}
const mapStateToProps = (state: RootState) => ({
    sheet: state.sheetSlice
})

export default connect(mapStateToProps, null)(Sheet) as unknown as React.FC<SheetProps>
